.MainStoryContainer{
	background-color: #ffffff;	
	display: block;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	margin-left:auto;
	margin-right:auto;
	margin-top: 20px;
	margin-bottom: 20px;
	overflow: auto;	
}

.mainStoryDiv{
	width: 100%;
	/* height: 150px; */
	border-width: 1px;
	margin: auto;
	float:left;


}

.focus{
	font-size: 2vw;
	font-weight: bolder;
	color: #1F2B5D;
}

.mainStoryDivHeader{
	text-align: center;
	margin-left: 10%;
	margin-right:10%;
	font-size: 3vw;
	font-weight: bolder;
	color: #1F2B5D;
}

.mainStoryDivBody{
	text-align: center;
	margin-left: 10%;
	margin-right:10%;
	font-size: 1.8vw;
}

@media screen and (max-width: 960px){
	.focus{
		font-size: 20px;
	}
	
	.mainStoryDivHeader{
		font-size: 30px;
	}
	
	.mainStoryDivBody{
		font-size: 18px;
	}
}