.NavbarItems {
    background: #1F2B5D;
    min-height: 150px;
    display: flex;
    justify-content: center;
    align-content: center;
    font-size: 1.2rem;
}

.navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    margin-top:20px;
    cursor: pointer;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style:none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right:2rem;
    margin-top: 3em;
}

.nav-links{
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover{
    color: #ffce07;
    background-color: #353e61;
    border-radius: 4px;
    transition: all 0.2s ease-out;
    text-decoration:none;
}

.fa-bars{
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px){
    .NavbarItems {
        /* position: relative; */
        height: 160px;
    }

    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 100px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active{
        background: #1F2B5D;
        left:0;
        opacity:1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover{
        background-color: #353e61;
        border-radius: 0;
    }

    .navbar-logo{
        margin-top:0px;
        position: absolute;
        top: 0;
        left:0;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times{
        color:#fff;
        font-size: 2rem;
    }

