.postContainer{
    background-color: white;
	display: block;
	justify-content: center;
	align-items: center;
	margin: auto;
    margin-top: 20px;
    width: 100%;
    display:flex;
    justify-content: space-around;
}


.postDiv{
	width: 49%;
	height: 655px;
	border-width: 1px;
    float:left;
	background-color: lightblue;
}

.eventImage{
    width:100%;
}

.event1{
    width: 49%;
    display: grid;
    /* grid-template-columns: 60% 40%; */
    grid-template-columns: 3fr 2fr;
    background-image: url(../../images/Event1.JPG);
    background-size: cover;
    background-repeat: no-repeat;
	height: 500px;
    border-radius: 5px;
    float:left;
    background-color: lightblue;
    justify-items: stretch;
    align-items:stretch;
}

.event2{
    width: 49%;
    display: grid;
    grid-template-columns: 3fr 1fr;
    background-image: url(../../images/Event2.jpg);
    background-size: cover;
    background-repeat: no-repeat;
	height: 500px;
	border-radius: 5px;
    float:left;
    background-color: lightblue;
    justify-items: stretch;
    align-items:stretch;
}

.event3{
    width: 49%;
    display: grid;
    grid-template-columns: 3fr 2fr;
    background-image: url(../../images/Event3.jpg);
    background-size: cover;
    background-repeat: no-repeat;
	height: 500px;
	border-radius: 5px;
    float:left;
    background-color: lightblue;
    justify-items: stretch;
    align-items:stretch;
}

.content{
    background: #ffffff;
    z-index: 10;
    padding-left: 10%;
}

.box1{
    grid-column: 1/2;
    grid-row:1/2;
}

.box2{
    grid-row:2/6;
    opacity: 0.7;
}

.box3{
    grid-row:2/6;
    
}

.box4{
    grid-column: 1/2;
    grid-row:6/10;
}

.header{
    padding-top: 10%;
    font-size: 1.8vw;
	font-weight: bolder;
	color: #1F2B5D;
}

.title{
    font-size: 2vw;
    font-weight: bold;
	color: #1F2B5D; 
}

.body{
    font-size: 1.5vw;
	color: #1F2B5D;
}

@media screen and (max-width: 960px){
    .header{
        font-size: 20px;
    }
    
    .title{
        font-size: 24px;
    }
    
    .body{
        font-size: 18px;
    }
}