.quickLinkContainer{
	background-color: rgb(241, 243, 247);	
	display: block;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	margin-left:auto;
	margin-right:auto;
	margin-top: 20px;
	/* height: 250px; */
	overflow: auto;	
}

.quickLinkDiv{
	width: 33%;
	/* height: 150px; */
	border-width: 1px;
	margin: auto;
	float:left;


}

.quickLinkDivHeader{
	text-align: center;
	font-size: 20px;
	margin-left: 10%;
	margin-right:10%;
	margin-top: 10%;
}

.quickLinkDivBody{
	text-align: center;
	font-size: 14px;
	margin-left: 10%;
	margin-right:10%;
	overflow-wrap: break-word;
}
